export default function Page() {
  return (
    <div style={{ fontFamily: "Calibri, 'Segoe UI', Roboto, sans-serif" }}>

      <style>{`
        /* --- Light Rich Garden Header --- */
        :root{
          --garden-1: #e8f9f1;
          --garden-2: #fffaf0;
          --garden-3: #f7fff9;
          --accent: rgba(25, 130, 96, 0.08);
        }

        body { margin: 0; }

        .header-wrap {
          background: linear-gradient(90deg, var(--garden-1) 0%, var(--garden-2) 50%, var(--garden-3) 100%);
          box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
        }

        /* subtle leaf motif using SVG data URI (very low opacity) */
        .header-wrap::before{
          content:"";
          position:absolute;
          left:0; right:0; top:0;
          height: 110px;
          pointer-events:none;
          background-image:
            radial-gradient(circle at 5% 10%, rgba(255,255,255,0.6) 0, rgba(255,255,255,0.0) 40%),
            url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='600' height='120' viewBox='0 0 600 120'><g opacity='0.06' fill='none' stroke='%23084d3a' stroke-width='2'><path d='M30 60 C70 10, 150 10, 220 60'/><path d='M480 80 C520 40, 580 40, 620 80'/></g></svg>");
          background-repeat: no-repeat;
          background-position: center top;
          background-size: cover;
          mix-blend-mode: normal;
        }

        .header {
          max-width: 1300px;
          margin: 0 auto;
          position: relative;
          padding: 14px 22px;
          display: grid;
          grid-template-columns: 1fr 1fr 1fr 1fr;
          align-items: center;
          gap: 12px;
        }

        .col-left { display:flex; align-items:center; gap:14px; }
        .col-center { display:flex; justify-content:center; align-items:center; gap:12px; }
        .col-right { display:flex; justify-content:flex-end; align-items:center; gap:16px; }

        /* Title + butterfly container */
        .title-wrap { position: relative; display:inline-block; line-height:1; }
        .title-text {
          font-size: 34px;
          font-weight: 700;
          margin: 0;
          white-space: nowrap;
          color: #0b3d2e;
        }

        /* Butterfly: larger, slightly above and touching the tail */
        @keyframes gentleFlutter {
          0%   { transform: translateY(0) scaleX(-1) rotate(-2deg); }
          50%  { transform: translateY(-6px) scaleX(-1) rotate(1deg); }
          100% { transform: translateY(0) scaleX(-1) rotate(-2deg); }
        }
        .butterfly {
          position: absolute;
          right: -18px;      /* nudges butterfly toward the title tail */
          top: -18px;        /* lifts butterfly above title line */
          width: 80px;       /* you requested 80px */
          height: auto;
          transform-origin: center;
          animation: gentleFlutter 3.8s ease-in-out infinite;
          pointer-events: none;
          filter: drop-shadow(0 4px 8px rgba(0,0,0,0.08));
        }

        /* Buttons */
        .reg-btn, .login-btn {
          padding: 9px 16px;
          border-radius: 10px;
          color: #fff;
          font-weight: 800;
          text-decoration: none;
          display:inline-block;
          box-shadow: 0 3px 8px rgba(0,0,0,0.08);
        }
        .reg-btn { background: #e02424; }
        .login-btn { background: #0b5cff; }

        .forgot {
          color: #0b3d2e;
          font-weight: 700;
          text-decoration: none;
        }

        /* FOF block */
        .fof-block { display:flex; flex-direction: column; align-items:center; gap:8px; }
        .fof-title { font-weight: 800; font-size: 19px; color: #0b3d2e; }

        /* A2Z */
        .a2z-block { display:flex; flex-direction:column; align-items:center; gap:6px; }
        .a2z-title { font-weight: 700; font-size: 14px; color: #0b3d2e; }

        /* logos clickable reset */
        a.logo-link { display:inline-block; }

        /* language selector */
        .lang-select {
          padding: 6px 10px;
          border-radius: 8px;
          border: 1px solid rgba(0,0,0,0.12);
          background: #fff;
          font-weight: 700;
        }

        /* logo sizes */
        .logo-100 { width: 100px; height: 100px; object-fit: contain; display:block; }
        .square-panther { height: 120px; width: auto; object-fit: contain; display:block; }

        /* responsive */
        @media (max-width: 980px) {
          .header { grid-template-columns: 1fr; gap:10px; padding: 12px; }
          .col-left, .col-center, .col-right { justify-content:center; }
          .title-text { font-size: 26px; }
          .butterfly { top: -12px; right: -12px; width: 64px; }
          .logo-100 { width: 88px; height: 88px; }
          .square-panther { height: 90px; }
        }
      `}</style>

      {/* Header wrapper (adds subtle leaf motif via ::before) */}
      <div className="header-wrap">

        {/* header grid */}
        <header className="header">

          {/* COLUMN 1: BLF logo + title + butterfly */}
          <div className="col-left">
            <a href="#" className="logo-link" aria-label="Beautiful Life Home">
              <img
                src="/assets/img/logos/header1/BEAUTIFUL LIFE LOGO 2.jpg"
                alt="BLF Logo"
                className="logo-100"
                style={{ borderRadius: "50%" }}
              />
            </a>

            <span className="title-wrap">
              <a href="#" style={{ textDecoration: "none" }}>
                <h1 className="title-text">Beautiful Life</h1>
              </a>

              {/* Butterfly: faces left (scaleX(-1)) and gently flutters */}
              <img
                src="/assets/img/logos/header1/butterfly (2).gif"
                alt="Butterfly"
                className="butterfly"
                style={{ width: 80 }}
              />
            </span>
          </div>

          {/* COLUMN 2: Register / Login / Forgot */}
          <div className="col-center">
            <a href="#" className="reg-btn">Register</a>
            <a href="#" className="login-btn">Login</a>
            <a href="#" className="forgot">Forgot Password</a>
          </div>

          {/* COLUMN 3: Panther (FOF) */}
          <div style={{ display: "flex", justifyContent: "center" }}>
            <div className="fof-block">
              <a href="#" className="logo-link" aria-label="Friends of Friends">
                <img
                  src="/assets/img/logos/header1/fof.png" /* change to fof.jpg if necessary */
                  alt="FOF Panther"
                  className="square-panther"
                />
              </a>
              <div className="fof-title">Friends of Friends (FOF)</div>
            </div>
          </div>

          {/* COLUMN 4: A2Z + Language selector */}
          <div className="col-right">
            <div className="a2z-block">
              <a href="#" className="logo-link" aria-label="A2Z Group Companies">
                <img
                  src="/assets/img/logos/header1/a2zgroups.jpg"
                  alt="A2Z Logo"
                  className="logo-100"
                  style={{ width: 100, height: 100 }}
                />
              </a>
              <div className="a2z-title">A2Z Group Companies</div>
            </div>

            {/* language selector: emoji flags + name */}
            <select className="lang-select" defaultValue="en" aria-label="Select language">
              {/* Indian (alphabetical-ish group) */}
              <option value="as">ðŸ‡®ðŸ‡³ Assamese</option>
              <option value="bn">ðŸ‡®ðŸ‡³ Bengali</option>
              <option value="en">ðŸ‡¬ðŸ‡§ English</option>
              <option value="gu">ðŸ‡®ðŸ‡³ Gujarati</option>
              <option value="hi">ðŸ‡®ðŸ‡³ Hindi</option>
              <option value="kn">ðŸ‡®ðŸ‡³ Kannada</option>
              <option value="ks">ðŸ‡®ðŸ‡³ Kashmiri</option>
              <option value="ml">ðŸ‡®ðŸ‡³ Malayalam</option>
              <option value="mr">ðŸ‡®ðŸ‡³ Marathi</option>
              <option value="or">ðŸ‡®ðŸ‡³ Odia</option>
              <option value="pa">ðŸ‡®ðŸ‡³ Punjabi</option>
              <option value="sa">ðŸ‡®ðŸ‡³ Sanskrit</option>
              <option value="ta">ðŸ‡®ðŸ‡³ Tamil</option>
              <option value="te">ðŸ‡®ðŸ‡³ Telugu</option>
              <option value="ur">ðŸ‡®ðŸ‡³ Urdu</option>

              {/* International */}
              <option value="ar">ðŸ‡¸ðŸ‡¦ Arabic</option>
              <option value="zh">ðŸ‡¨ðŸ‡³ Chinese</option>
              <option value="nl">ðŸ‡³ðŸ‡± Dutch</option>
              <option value="fr">ðŸ‡«ðŸ‡· French</option>
              <option value="de">ðŸ‡©ðŸ‡ª German</option>
              <option value="it">ðŸ‡®ðŸ‡¹ Italian</option>
              <option value="ja">ðŸ‡¯ðŸ‡µ Japanese</option>
              <option value="ko">ðŸ‡°ðŸ‡· Korean</option>
              <option value="pt">ðŸ‡µðŸ‡¹ Portuguese</option>
              <option value="ru">ðŸ‡·ðŸ‡º Russian</option>
              <option value="es">ðŸ‡ªðŸ‡¸ Spanish</option>
              <option value="tr">ðŸ‡¹ðŸ‡· Turkish</option>
            </select>
          </div>

        </header>
      </div>

      {/* HERO placeholder */}
      <main style={{ textAlign: "center", marginTop: 36 }}>
        <div>Hero section will be added here.</div>
      </main>
    </div>
  );
}
